.aboutBlankOpener {
    position: absolute; // Absolute positioning
    width: calc(50% - (1.5 * $uniMargin)); // Width adjusted for uniform margin
    height: calc(100% - ($botBarHeight + $uniMargin) - $uniMargin); // Height considering uniform margin and waybar height
    bottom: calc($botBarHeight + $uniMargin);
    right: $uniMargin;
    border: $borderThickness solid $borderCol; // Border styling
    border-radius: $borderRad;
    box-shadow: $shadowRad $shadowCol;
    background-color: $windowCol;
    backdrop-filter:
        blur($windowBlur)
        brightness($windowBrightness)
        contrast($windowContrast);
    box-sizing: border-box; // Ensures consistent box model
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: all $transitionSpeed ease;
    &:hover {
        user-select: none;
        width: calc(50% - (1.5 * $uniMargin) * $hoverCoefficient + $hoverConst);
        height: calc(((100% - ($botBarHeight + $uniMargin) - $uniMargin) * $hoverCoefficient) + $hoverConst); // Hover effect for height
        right: calc($uniMargin - (((100% - (2 * $uniMargin)) * ($hoverCoefficient - 1)) / 2) - ($hoverConst / 2));
        bottom: calc((($botBarHeight + $uniMargin) * $hoverCoefficient) + $hoverConst / 2);
        border-radius: calc(($borderRad * $hoverBorderRad) + (((($waybarHeight - ($uniMargin) * $hoverCoefficient) + ($hoverConst * $uniMargin / 1vh))) - ($waybarHeight - ($uniMargin))) + (($borderRad * $hoverBorderRad) / ((($borderRad * $hoverBorderRad) / 1px) / 2)));
        box-shadow: $hoverShadowRad $hoverShadowCol; // Box shadow styling
        border: $hoverBorderThickness solid $hoverBorderCol; // Border styling on hover
        background-color: $hoverWindowCol;
        backdrop-filter:
            blur($hoverWindowBlur)
            brightness($hoverWindowBrightness)
            contrast($hoverWindowContrast);
    }
}


.standardOpener {
