    width: calc(50% - (1.5 * $uniMargin)); // Width adjusted for uniform margin
    height: calc(100% - ($botBarHeight + $uniMargin) - $uniMargin); // Height considering uniform margin and waybar height
    bottom: calc($botBarHeight + $uniMargin);
    left: $uniMargin;
    border: $borderThickness solid $borderCol; // Border styling
    border-radius: $borderRad;
    box-shadow: $shadowRad $shadowCol;
    background-color: $windowCol;
